import React, { Component } from 'react'
import { Icon } from 'antd'
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { getAction } from '../../../axios';
import { carouselTitleAction } from './../../../redux/actions/carouselTitleAction';
import { majorMenuAction } from './../../../redux/actions/majorMenuAction'
import { isShowMajorMenuAction } from './../../../redux/actions/isShowMajorMenuAction'
import { level2MenuAction } from '../../../redux/actions/level2MenuAction';

class Theader extends Component {
    state={
        menus:[],
        currentMenu:'',
        majorMenus: [],
        
        level2Menus:[],
        level2MenusOld: [],
        currentLevel2Menu: '',
        isShowLevel2Menu: false,
    }
    componentDidMount = () => {
        let menus=[{
            id:'0001',
            title:'达标指标',
            url:'/dabiao',
        },{
            id:'0002',
            title:'向好指标',
            url:'/xianghao',
        },{
            id:'0003',
            title:'向差指标',
            url:'/xiangcha',
        },{
            id:'0004',
            title:'预警指标',
            url:'/yujing',
        },{
            id:'0005',
            title:'学校层面',
            url:'/xuexiao',
        },{
            id:'0006',
            title:'教师层面',
            url:'/jiaoshi',
        },{
            id:'0007',
            title:'学生层面',
            url:'/xuesheng',
        },{
            id:'0008',
            title:'专业层面',
            url:'/zhuanye',
        },{
            id:'0009',
            title:'课程层面',
            url:'/kecheng',
        }]
        let data1=menus.map((item,index)=>{
            item.key=index+1
            return item
        })

        let majorMenus=[{
            id:'1',
            title:'会计专业',
            url: 'kuaiji'
        },{
            id:'2',
            title:'计算机网络技术专业',
            url: 'jisuanji'
        },{
            id:'3',
            title:'汽车电子技术专业',
            url: 'qichedianzi'
        },{
            id:'4',
            title:'汽车营销与服务专业',
            url: 'qicheyinxiao'
        },{
            id:'5',
            title:'汽车运用维修专业',
            url: 'yicheyunyong'
        },{
            id:'6',
            title:'数控技术专业',
            url: 'shukong'
        }]

        let data2=majorMenus.map((item,index)=>{
            item.key=index+1
            return item
        })

        // if(localStorage.getItem('menuItem')){
        //     this.setState({
        //         menus:data,
        //         currentMenu:JSON.parse(localStorage.getItem('menuItem')).title
        //     })
        // }else{
        //     this.setState({
        //         menus:data,
        //         currentMenu:menus[0].title
        //     })
        // }
        // 暂时注释掉localstoreage,暂时不做持久化 
        this.setState({
            menus:data1,
            currentMenu:menus[0].title,
            majorMenus:data2
        })
        this.getMajorData();
    }
    getMajorData(){
        getAction('/bigdata/basic/info/queryMajorList').then(res => {
            if(res.success && res.obj[0]){
                this.setState({
                    level2Menus: res.obj,
                    level2MenusOld: res.obj,
                    currentLevel2Menu: res.obj[0].name
                })
                this.props.changeCurrentLevel2Menu(res.obj[0]);
            }
        })
    }
    getCourseData(){
        getAction('/bigdata/basic/info/queryCourseList').then(res => {
            if(res.success && res.obj[0]){
                this.setState({
                    level2Menus: res.obj,
                    level2MenusOld: res.obj,
                    currentLevel2Menu: res.obj[0].name
                }, ()=>{
                    console.log(this.state.level2Menus, 333);
                })
                this.props.changeCurrentLevel2Menu(res.obj[0]);
            }
        })
    }
    // 处理页面跳转，同时将当前的菜单信息存入本地
    handleGoTo=(item)=>{
        localStorage.setItem('menuItem',JSON.stringify(item))
        // console.log(this.state.level2MenusOld);
        this.setState({
            currentMenu:item.title,
            // level2Menus: this.state.level2MenusOld
        })
        this.props.history.push(item.url)
        if(item.id !== '0001' && item.id !== '0002' && item.id !== '0003' && item.id !== '0004'){
            this.props.resetCarouselTitle(-1);
        }else {
            this.props.resetCarouselTitle(0);
        }
        this.props.filterMajor("0");
        this.props.toggleMajorMenu(false);
        if(item.id === '0008'){
            this.getMajorData();
            this.setState({isShowLevel2Menu: true});
        }else if(item.id === '0009'){
            this.getCourseData();
            this.setState({isShowLevel2Menu: true});
        }else{
            this.setState({isShowLevel2Menu: false});
        }
    }
    handleFilterMajorMenu=(item)=>{
        this.props.filterMajor(item.id);
    }
    gotoWel=()=>{
        this.props.history.push('/welcome')
    }
    changeValue = (e) => {
        this.setState({currentLevel2Menu: e.target.value});
        let newLevel2Menus = JSON.parse(JSON.stringify(this.state.level2MenusOld));
        let level2Menus = newLevel2Menus.filter(item => item.name.indexOf(e.target.value) > -1)
        this.setState({level2Menus});
    }
    handleChangeLevel2Menu = (item) => {
        this.setState({currentLevel2Menu: item.name})
        this.props.changeCurrentLevel2Menu(item);
        this.setState({level2Menus: this.state.level2MenusOld});
    }
    render() {
        const {menus,currentMenu,majorMenus, currentLevel2Menu, level2Menus, isShowLevel2Menu}=this.state
        let {currentMajorMenu, majorMenuStatus, carouselTitle} = this.props;
        return (
            <div className='header-wrapper'>
                <div className='home'>
                    <Icon onClick={this.gotoWel} type="home" theme='filled' style={{ fontSize: 27, color: '#fff', marginLeft: 20, cursor: 'pointer' }} />
                </div>
                <div className='other'>
                    <div className='other-title'>
                        质量体系诊断与改进平台
                    </div>
                    <div className='other-menu'>
                        {currentMenu}
                        <div className='other-sub-menu'>
                            {
                                menus.map(item=><div key={item.key} onClick={()=>{this.handleGoTo(item)}} className={currentMenu === item.title ? 'item active':'item'}>{item.title}</div>)
                            }
                        </div>
                    </div>
                    <div className='other-menu other-menu-type' style={ {display: majorMenuStatus?'block':'none'} }>
                        {currentMajorMenu}
                        <div className='other-sub-menu other-sub-menu-type'>
                            {
                                majorMenus.map(item=><div key={item.key} onClick={()=>{this.handleFilterMajorMenu(item)}} className={currentMajorMenu === item.title ? 'item active':'item'}>{item.title}</div>)
                            }
                        </div>
                    </div>
                    <div className='other-menu other-menu-type' style={ {display: isShowLevel2Menu?'block':'none'} }>
                        <input type="text" value={currentLevel2Menu} style={{background: 'transparent', border: 'none', textAlign: 'center', outline: 'none'}} onChange={this.changeValue}/>
                        <div className='other-sub-menu other-sub-menu-type'>
                            {
                                level2Menus.map(item=><div key={item.code} onClick={()=>{this.handleChangeLevel2Menu(item)}} className={currentLevel2Menu === item.name ? 'item active':'item'}>{item.name}</div>)
                            }
                        </div>
                    </div>

                    <div className={`other-line ${majorMenuStatus || isShowLevel2Menu?'other-line-toggle':null}`}>
                        
                    </div>
                    <div className='carousel-title'>{ carouselTitle }</div>
                </div>
            </div>
        )
    }
}



export default withRouter(connect((state) => {
    return {
        carouselTitle: state.carouselTitleReducer.carouseltitle,
        majorMenuStatus: state.isShowMajorMenuReducer.majorMenuStatus,
        currentMajorMenu: state.majorMenuReducer.currentMajorMenu,
        currentLevel2Menu: state.level2MenuReducer.currentLevel2Menu
    }
}, (dispatch) => {
    return {
        resetCarouselTitle(type){
            const action = carouselTitleAction(type);
            dispatch(action);
        },
        filterMajor(type){
            const action = majorMenuAction(type);
            dispatch(action);
        },
        toggleMajorMenu(bool){
            const action = isShowMajorMenuAction(bool);
            dispatch(action);
        },
        changeCurrentLevel2Menu(obj){
            const action = level2MenuAction(obj);
            dispatch(action);
        }
    }
})(Theader))